<template>
  <div>
    <div class="container vw-100 vh-100 c-container-color d-flex col-12
      justify-content-center align-items-center">
      <div class="c-panel-container col-xl-6 col-lg-8 col-md-10
        d-sm-flex justify-content-between align-items-center">
        <div class="card col-md-5 shadow pt-5 pb-3 my-3 my-sm-n3">
          <div class="c-panel-style">
          </div>
          <div class="card-body">
            <h5 class="card-title font-weight-bold">全部病患</h5>
            <p class="card-text">確認
              <span class="text-primary font-weight-bold">所有</span>
              病患狀態</p>
            <a href="#" @click.prevent="enterDashboard('all')" class="btn btn-primary">確認</a>
          </div>
        </div>
        <div class="card col-md-5 shadow pt-5 pb-3">
          <div class="c-panel-style">
          </div>
          <div class="card-body">
            <h5 class="card-title font-weight-bold">分組病床</h5>
            <p class="card-text">確認
              <span class="text-secondary font-weight-bold">各組</span>
              病患狀態</p>
            <a href="#" @click.prevent="enterDashboard('part')" class="btn btn-primary">確認</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    enterDashboard(patientQty) {
      const vm = this;
      vm.$store.dispatch('loadingOverlay');
      localStorage.setItem('patientQty', JSON.stringify({ patientQty: patientQty }));
      setTimeout(() => {
        vm.$store.dispatch('loadingOverlay');
        vm.$router.push({ path: 'dashboard' });
      }, 750);
    },
  },
};
</script>
